// index.js

!function(window, document, $) {

    'use strict';

    var Index = function() {
        this.$el = {
            win: $(window),
            doc: $(document),
            body: $('body'),
            contents: $('.blog-contents'),
            header: $('.blog-header'),
            search: $('.blog-search')
        }
        this.scrollTop = 0;
        this.timer = false;

        this._init();
    };

    Index.prototype._init = function() {
        this.popupSearch();
        this.initEvents();
    };

    Index.prototype.onLoadEvent = function() {
        this.$el.body.addClass('is-loaded');
    };

    Index.prototype.popupSearch = function() {
        var self = this;
        var changeFlag = false;

        this.$el.header.find('.icon-search').on('click', function(e) {
            e.preventDefault();

            if (!changeFlag) {
                openPopup();
            } else {
                closePopup();
            }
        });

        this.$el.contents.on('click', function(e) {
            if (changeFlag) {
                e.preventDefault();

                closePopup();
            }
        });

        // pushed ESC key
        this.$el.win.keyup(function(e){
            if (e.keyCode === 27) {
                closePopup();
            }
        });

        function openPopup() {
            self.$el.search.fadeIn(0, function() {
                changeFlag = true;
                $(this).find('input').focus();
            });
        }

        function closePopup() {
            self.$el.search.fadeOut(0, function() {
                changeFlag = false;
            });
        }
    };

    Index.prototype.initEvents = function() {
        this.$el.win.on('load', $.proxy(this.onLoadEvent, this));
    };

    $(function() {
        new Index();
    });

}(this, this.document, jQuery);
